import React from 'react';
import {
    withRouter,
} from "react-router-dom";


class Registrar extends React.Component {
    state = {
        nombre: '',
        telefono: '',
        programa: '22/22'
    }

    componentDidMount() {
        this.contactos = JSON.parse(localStorage.getItem('contactos'))
    }


    back = () => {
        this.props.history.goBack()

    }

    registrar = () => {
        if (this.state.telefono === '') {
            alert('Ingresa un telefono')
            return;
        }
        if (this.state.nombre === '') {
            alert('Ingresa un nombre')
            return;
        }
        this.contactos.unshift({
            nombre: this.state.nombre,
            telefono: this.state.telefono,
            programa: this.state.programa
        })

        localStorage.setItem('contactos', JSON.stringify(this.contactos))
        this.props.history.goBack()

    }

    telefonoCambio = (t) => this.setState({ telefono: t.target.value })
    nombreCambio = (t) => this.setState({ nombre: t.target.value })
    programaCambio = (t) => this.setState({ programa: t.target.value })

    render() {
        return (
            <>
                <p id="welcome">Registrar un contacto</p>
                <div id="inputBlock">
                    <p id="label">Numero de teléfono</p>
                    <input
                        value={this.state.telefono}
                        onChange={this.telefonoCambio}
                        id="telefono" class="inputText" type="tel" />
                </div>

                <div id="inputBlock">
                    <p id="label">Nombre</p>

                    <input
                        value={this.state.nombre}
                        onChange={this.nombreCambio}
                        id="nombre" class="inputText" type="text" />
                </div>

                <div id="inputBlock">
                    <p id="label">Selecciona el programa</p>
                    <select
                        value={this.state.programa}
                        onChange={this.programaCambio}
                        id="programa">
                        <option value="22/22">22/22</option>
                        <option value="FAT40">FAT40</option>
                        <option value="U30">U30</option>
                    </select>
                </div>


                <button className="button" onClick={this.back}>cancelar</button>
                <button className="button" onClick={this.registrar}>registrar</button>
            </>
        )
    }
}


export default withRouter(Registrar);