import React from 'react';
import {
    Link
} from "react-router-dom";

class Home extends React.Component {
    state = {
        contactos: [],
        nombre: '',

    }

    componentDidMount() {
        if (localStorage.getItem('contactos') === null) localStorage.setItem('contactos', JSON.stringify([]))

        this.setState({
            contactos: JSON.parse(localStorage.getItem('contactos'))
        })

    }

    presentarme = (contacto) => {
        let nombre = contacto.nombre.split(' ')[0];
        let msg = encodeURI(`Buenos días ${nombre}, soy Diana Mendoza de Ludus Magnus, cómo estás?`)
        window.open(`https://wa.me/521${contacto.telefono}?text=${msg}`)
    }
    preguntar = (contacto) => {
        let msg = encodeURI(`Me da gusto, espero que te encuentres bien. La razón por la que nos comunicamos contigo es porque nos gustaría saber el motivo por el cual interrumpiste tu proceso y ver en qué podemos ayudarte para que regreses a tu programa ${contacto.programa}`)
        window.open(`https://wa.me/521${contacto.telefono}?text=${msg}`)
    }

    whatsapp = (contacto) => {
        window.open(`https://wa.me/521${contacto.telefono}?text=${encodeURI('.')}`)
    }
    borrar = (d) => {
        const contactos = Array.from(this.state.contactos).filter(c => {
            return !(c.nombre === d.nombre && c.telefono === d.telefono && c.programa === d.programa)
        })

        this.setState({ contactos })
        localStorage.setItem('contactos', JSON.stringify(contactos))
    }

    renderContactos = () => this.state.contactos.filter(contacto =>
        contacto.nombre.toLowerCase().startsWith(this.state.nombre.toLowerCase())
    ).map((contacto, k) =>
        <div className="contacto" key={contacto.telefono}>
            <div className="nombre">
                {contacto.nombre} | {contacto.telefono} | {contacto.programa}
            </div>

            <button className="button presentarme" onClick={() => this.presentarme(contacto)}>presentarme</button>
            <button className="button preguntar" onClick={() => this.preguntar(contacto)}>preguntar</button>
            <button className="button whatsapp" onClick={() => this.whatsapp(contacto)}>ws</button>
            <button className="button peligro" onClick={() => this.borrar(contacto)}>borrar</button>

        </div>
    )



    render() {
        return (
            <>

                <p id="welcome">Bienvenida Diana</p>

                <div className="title">
                    <Link to="/registrar">
                        <button className="button">Registrar</button>
                    </Link>
                </div>


                <input
                    value={this.state.nombre}
                    onChange={(e) => this.setState({ nombre: e.target.value })}
                    className="inputText upMargin"
                    placeholder="filtrar por nombre" />

                <div className="contactos">
                    {this.state.contactos.length === 0 ? 'no tienes contactos' : this.renderContactos()}
                </div>
            </>
        )
    }
}


export default Home;